.card {
    border: none;
    border-radius: .625rem;

    picture {
        border-radius: inherit;
    }

    .card-body {
        position: relative;
        min-height: 200px;
        .pos-icon{
            position: absolute;
            bottom: 10px;
            right: 10px;
        }
        .min-height-duyuru {
            height: 100px !important;
            overflow-y: auto;
          }
    }

    .card-content-border {
        position: absolute;
        border-left: 2px solid #FFF;
        padding-left: 20px;
        min-height: 75%;
    }

    .card-img-top {
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
        width: 100%;
        object-fit: cover;
        margin-bottom: -3rem;
    }

    &.custom-card {
        .card-body {
            min-height: auto;
        }
    }

}